.home {
  text-align: center;
  .text01 {
    display: flex;
    justify-content: center;
    span {
      font-size:40px;
    }
    p {
      font-size:45px;
      padding:20px;
      text-align:center;
      font-weight:500;
      line-height: 1.2;
    }
  }
  .avatarImg {
    width: 200px;
    padding: 40px 0 80px 0;
  }
  .iconWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    a {
      font-size: 40px;
      :hover {
        color:$pointClr;
      }
    }
  }
  .btnDown {
    margin:50px auto 0 auto;
    display:inline-block;
    padding:10px 25px;
    border:1px solid $pointClr;
    border-radius:25px;
    font-size:16px;
    font-weight: 600;
    color: $pointClr;
    cursor: pointer;
    &:hover {
      background: $pointClr;
      color: #FFF;
    }
  }
}

/* Tablet */
@media only screen and (max-width: 1119px) {

}

/* Mobile */
@media only screen and (max-width: 800px) {
  .home {
    .text01 {
      span {
        font-size:10vw;
      }
      p {
        font-size:7vw;
        padding:5vw;
      }
    }
    .avatarImg {
      width: 50vw;
      padding: 10vw 0 20vw 0;
    }
    .iconWrap {
      gap: 10vw;
      a {
        font-size: 10vw;
      }
    }
    .btnDown {
      margin:10vw auto 0 auto;
      padding:3vw 7vw;
      border-radius:20vw;
      font-size:4vw;
    }
  }
}