.about {
  h2 {
    font-size:24px;
    font-weight: 700;
    text-align: center;
  }
  .text {
    padding:80px 0;
    background-color: #333;
    p {
      color: #FFF;
      font-size: 20px;
      text-align: center;
      line-height: 1.6;
      font-weight: 500;
      strong {
        font-weight:700;
      }
      br.mobr {
        display: none;
      }
      br.pcbr {
        display: block;
      }
    }
    p + p {
      padding-top:30px;
    }
  }
  .skills {
    padding:60px;
    .skillWrap {
      padding:50px 190px 0 190px;
      text-align: center;
      span {
        display: inline-block;
        margin:7px;
        padding:0 20px;
        height:36px;
        line-height: 30px;
        font-size: 20px;
        border-radius:18px;
        border:1px solid #333;
        font-weight: 600;
      }
    }
  }
  .desciption {
    text-align: center;
    padding-top: 60px;
    li {
      font-size:22px;
      font-weight: 500;
      line-height: 1.3;
      margin-bottom:25px;
      .ico {
        padding-right: 10px;
        font-size: 20px;
        color:$pointClr;
      }
    }
  }
  .experience {
    padding:50px;
    ul {
      position: relative;
      margin:40px 0;
      padding: 0 40px;
      li {
        position: relative;
        span {
          font-size:16px;
          font-weight: 500;
          color:$pointClr;
        }
        p {
          font-size:20px;
        }
        .txtBox {
          padding:10px;
          line-height: 1.4;
          p {
            position: relative;
            padding-left:15px;
            font-size: 17px;
            &:after {
              display: block;
              content: "";
              position: absolute;
              left:0;
              top:13px;
              width:5px;
              height:1px;
              background: #333;
            }
          }
        }
        &:after {
          display:block;
          width:10px;
          height:10px;
          border-radius: 50%;
          content:"";
          background: #777777;
          position: absolute;
          top:50%;
          transform: translateY(-50%);
          left:-44px;
        }
      }
      li + li {
        margin-top:40px;
      }
      &:after {
        display:block;
        width:2px;
        height:100%;
        content:"";
        background: #777777;
        position: absolute;
        top:0;
        left:0;
      }
    }
  }
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .about {
    h2 {
      font-size:7vw;
    }
    .text {
      padding:15vw 0;
      p {
        font-size: 5vw;
        strong {
          font-weight:700;
        }
        br.pcbr {
          display:none;
        }
        br.mobr {
          display: block;
        }
      }
      p + p {
        padding-top:5vw;
      }
    }
    .skills {
      padding:8vw;
      .skillWrap {
        padding:5vw;
        span {
          margin:1vw;
          padding:0 4vw;
          height:7vw;
          line-height: 6svw;
          font-size: 4vw;
          border-radius:3.5vw;
        }
      }
    }
    .desciption {
      padding-top: 6vw;
      li {
        font-size:4vw;
        margin-bottom:3vw;
        .ico {
          padding-right: 2vw;
          font-size: 3vw;
        }
      }
    }
    .experience {
      padding:5vw;
      ul {
        margin:4vw 0;
        padding: 0 4vw;
        li {
          span {
            font-size:3vw;
          }
          p {
            font-size:20px;
          }
          .txtBox {
            padding:2vw;
            p {
              padding-left:3vw;
              font-size: 4vw;
              &:after {
                top:3vw;
                width:1.8vw;
                height:0.2vw;
              }
            }
          }
          &:after {
            width:3vw;
            height:3vw;
            left:-5.4vw;
          }
        }
        li + li {
          margin-top:40px;
        }
        &:after {
          display:block;
          width:2px;
          height:100%;
          content:"";
          background: #777777;
          position: absolute;
          top:0;
          left:0;
        }
      }
    }
  }
}