$pointClr : #ff4747;

.header {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding:50px 0;
  .logo {
    position: absolute;
    top:center;
    left:0;
    .logoImg {
      width:50px;
    }
  }

  .menuWrap {
    display: flex;
    justify-content: center;
    align-items: center;
    .menu {
      display: flex;
      justify-content: center;
      align-items: center;
      li {
        a {
          display: block;
          font-size:20px;
          font-weight:bold;
          margin:0 20px;
          color: #333;
        }
        a.active, &:hover a {
          position: relative;
          color: $pointClr;
          &:after {
            display: block;
            content:"";
            position:absolute;
            bottom:-5px;
            left:0;
            width: 100%;
            height:2px;
            background: $pointClr;
          }
        }
      }
    }
  }

  .moMenuBtn {
    display: none;
    position: fixed;
    top:5vw;
    right:5vw;
    width: 40px;
    z-index: 5;
    input[id=trigger] {
      display: none;
      &:checked + label[for=trigger] span:nth-child(1) {top:50%;transform:rotate(45deg);}
      &:checked + label[for=trigger] span:nth-child(2) {opacity:0;}
      &:checked + label[for=trigger] span:nth-child(3) {top:50%;transform:rotate(-45deg);}
    }
    label[for=trigger] {
      display:block;
      width:40px;
      height:30px;
      cursor:pointer;
      z-index:5;
      span {
        position:absolute;
        width:100%;
        height:2px;
        background:#333333;
        transition:0.3s;
        &:nth-child(1) {top:0;}
        &:nth-child(2) {top:50%;}
        &:nth-child(3) {top:100%;}
      }
    }
  }

  .icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left:20px;
    button {
      width: 40px;
      height: 40px;
      cursor: pointer;
      .icoMoon {
        font-size: 34px;
        color: #febc06;
      }
    }
    button + button {
      margin-left: 15px;
    }
  }
}

/* Tablet */
@media only screen and (max-width: 1200px) {
  
}

/* Mobile */
@media only screen and (max-width: 800px) {
  .header {
    padding: 10vw;
    .logo {
      position: absolute;
      top:center;
      left:0;
      .logoImg {
        width:15vw;
      }
    }
    .moMenuBtn {
      display: block;
    }
    .menuWrap {
      display: none;
      position: fixed;
      top:0;
      right:0;
      width:100%;
      height:100vh;
      background-color: #FFF;
      .menu {
        display: block;
        text-align: center;
        padding-top:30vw;
        li {
          a {
            
            font-size:8vw;
            margin:10vw;
          }
          a.active, &:hover a {
            &:after {
              display: none;
            }
          }
        }
      }
      .icon {
        position: absolute;
        bottom:15vw;
        left:0;
        right: 0;
        margin-left:0;
        button {
          width: 15vw;
          height: 15vw;
          cursor: pointer;
          .icoMoon {
            font-size: 15vw;
            color: #febc06;
          }
        }
        button + button {
          margin-left: 10vw;
        }
      }
    }
  }
}